<template>
  <div>
    <comp-header>
      <div class="imgBox"><img src="../assets/logotext.png" alt="" /></div>
    </comp-header>
    <div class="cinemaHeader" v-if="movieList.length">
      <div class="cinemaInfo">
        <h3 class="cname text-ellipsis">{{ cinemaInfoObj.nm }}</h3>
        <p class="caddr text-ellipsis">{{ cinemaInfoObj.addr }}</p>
      </div>
      <div class="addrIcon"><i class="bi bi-geo-alt"></i></div>
    </div>
    <div class="swiper" v-if="movieList.length">
      <comp-swiper @swiper-click="getSwiperId" :cmovie="movieList" :cindex="movieIndex"></comp-swiper>
    </div>
    <div class="movieInfo" v-if="movieList.length">
      <div class="movieHeader">
        <h3>{{movieList[movieIndex].nm}}</h3>
        <span>{{text}}</span>
      </div>
      <p>{{movieList[movieIndex].desc}}</p>
    </div>
    <div class="loadingBox" v-if="isLoadingShow">
      <comp-loadinganimate :wah="80"></comp-loadinganimate>
    </div>
  </div>
</template>


<script>
import CompHeader from "@/components/comp-header.vue";
import CompSwiper from "@/components/comp-swiper.vue";
import CompLoadinganimate from '@/components/comp-loadinganimate.vue'
export default {
  data() {
    return {
      cinemaId: 0,
      ci: 1,
      movieId: 0,
      date: "",
      movieList: [],
      movieIndex: 0,
      text: "",
      cinemaInfoObj: "",

      isLoadingShow: true,
    };
  },
  components: {
    CompHeader,
    CompSwiper,
    CompLoadinganimate,
  },
  methods:{
    getCinemaInfo() {
      axios.get("/cinema/detail",{params:{cinemaId:this.cinemaId}}).then((data) => {
        if (data.status == 200) {
            this.cinemaInfoObj = data.data.data;
            document.title = this.cinemaInfoObj.nm;
        }
      });
    },
    getCinemaMovie() {
      axios.get("/cinema/shows",{params:{cinemaId:this.cinemaId,ci:this.ci,channelId:4}}).then((data) => {
        if (data.status == 200) {
          this.isLoadingShow = false;
          this.movieList = data.data.data.movies;
          this.text = this.movieList[this.movieIndex].sc;
          this.movieId = this.movieList[this.movieIndex].id;
          console.log("movieId-->",this.movieId);
        }
      });
    },
    getSwiperId(i){
      this.movieIndex = i;
    }
  },
  created() {
    this.cinemaId = this.$route.query.cinemaId;
    if (this.$route.query.ci) {
      this.ci = this.$route.query.ci;
    }
    if (this.$route.query.movieId) {
      this.movieId = this.$route.query.movieId;
    }
    if (this.$route.query.date) {
      this.date = this.$route.query.date;
    }
    this.getCinemaInfo();
    this.getCinemaMovie();
  },
  watch: {
    movieIndex(){
      this.movieId = this.movieList[this.movieIndex].id;
      this.text = this.movieList[this.movieIndex].sc == '0.0'? this.movieList[this.movieIndex].wish + '':this.movieList[this.movieIndex].sc + '';
      console.log("movieId-->",this.movieId);
    }
  }
};
</script>

<style lang='scss' scoped>
@import "../assets/common/variable.scss";
.imgBox {
  width: 100px;
  img {
    width: 100%;
    vertical-align: text-bottom;
  }
}
.cinemaHeader {
  display: flex;

  .cinemaInfo {
    max-width: calc(100vw - 70px);
    padding: 15px;
    box-sizing: border-box;
  }

  .cname {
    font-size: 18px;
  }

  .caddr {
    font-size: 15px;
    color: #999;
    margin-top: 3px;
  }

  .addrIcon {
    width: 70px;
    flex: none;
    text-align: center;
    position: relative;

    i {
      font-size: 24px;
      color: $color;
      position: absolute;
      top: calc(50% - 31px / 2);
      right: calc(50% - 23px / 2);
    }

    &::after {
      content: "";
      display: block;
      width: 0;
      height: 30px;
      border-left: 1px solid $borderColor;
      position: absolute;
      left: 0;
      top: calc(50% - 30px / 2);
    }
  }
}
.movieInfo{
  font-size: 18px;
  text-align: center;
  padding: 15px;
  box-sizing: border-box;
  border-bottom: 1px solid $borderColor;
  .movieHeader{
    display: flex;
    justify-content: center;
    align-items: flex-end;

    span{
      font-size: 14px;
      font-weight: bold;
      margin-left: 5px;
      color: $color4;
    }
  }

  p{
    margin-top: 3px;
    font-size: 13px;
    color: #999;
  }
}
.text-ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.loadingBox {
  position: absolute;
  width: 100%;
  left: 0;
  top: calc(50% - 80px / 2);
}
</style>
